<template>
  <v-card elevation="0" tile :color="bg_color">
    <v-container class="py-0">
      <v-row justify="center">
        <v-card elevation="0" tile class="px-0" width="1200" :color="bg_color">
          <v-fab-transition>
            <v-btn :color="theme_color" dark absolute top left fab x-large>
              <v-icon color="white">{{ mian_icon }}</v-icon>
            </v-btn>
          </v-fab-transition>
          <v-card-text>
            <v-list class="mb-10" :color="bg_color">
              <v-subheader class="px-0 mx-0 mt-5">
                <v-row justify="center" class="px-0 mx-0">
                  <v-col cols="3">
                    <span :class="theme_text_color + ' title'"> Projects </span>
                  </v-col>
                  <v-spacer></v-spacer>
                </v-row>
              </v-subheader>
              <v-row class="px-0 mx-0">
                <v-col cols="12">
                  <div class="subtitle-1">
                    <a
                      :href="proj[proj_index].url"
                      target="_blank"
                      style="color: grey"
                    >
                      {{ proj[proj_index].name }}
                    </a>
                  </div>
                  <div
                    class="subtitle-1 font-weight-light"
                    style="height: 100px"
                  >
                    {{ proj[proj_index].description }}
                  </div>
                </v-col>
              </v-row>

              <v-carousel hide-delimiters height="500" v-model="proj_index">
                <v-carousel-item
                  v-for="(item, i) in proj"
                  :key="i"
                  reverse-transition="fade-transition"
                  transition="fade-transition"
                >
                  <v-row justify="center">
                    <v-col align="center" class="py-0">
                      <v-img height="500" width="1000" :src="item.img" contain>
                      </v-img>
                    </v-col>
                  </v-row>
                </v-carousel-item>
              </v-carousel>
            </v-list>
          </v-card-text>
        </v-card>
      </v-row>
      <v-divider></v-divider>
    </v-container>
  </v-card>
</template>

<script>
export default {
  name: "education",
  data: () => ({
    mian_icon: "mdi-monitor-dashboard",
    bg_color: "white",
    theme_color: "deep-purple darken-4",
    theme_text_color: "deep-purple--text text--darken-4",
    proj_index: 0,
    proj: [
      {
        name:
          "Visualization and Modeling Tools for Great Lakes Fish Monitoring and Surveillance Program",
        description: `The aim of this project is to provide research community and public an user-frendly interface for accessing 
          Great Lakes Fish Monitoring and Surveillance Program data 
          , also including data modeling tools. This project uses D3.js and Plotly.js for data visulizations, 
          Vue.js and Vuetify.js as web framework and UI, Python-flask as backend APIs.`,
        url: "https://glfmsp-viz.herokuapp.com/",
        img: require("@/assets/glfmsp_vue.png"),
      },
      {
        name:
          "TraPSA (Trajectory-based Potential Source Apportionment) Web Application",
        description: `The aim of this project is to provide a open-source toolkit for air pollution potential source locations apportionment analysis 
          based on trajectory ensemble receptor models. This project uses NOAA HYSPLIT4 model to generate trajectory endpoints. 
          The web application mainly uses Plotly.js, Vue.js and Vuetify.js as frontend, and Python as backend.`,
        url: "https://trapsa.herokuapp.com/",
        img: require("@/assets/trapsa_vue.png"),
      },
      {
        name:
          "TraPSA (Trajectory-based Potential Source Apportionment) Software",
        description: `This project is an early version of TraPSA Web Application. 
          The software is dveloped using MATLAB.`,
        url: "https://webspace.clarkson.edu/projects/TraPSA/public_html/",
        img: require("@/assets/trapsa.png"),
      },
    ],
  }),
};
</script>
